@import "../../scss/fonts.scss";


.create-room {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: center;
    align-self: center;
    grid-column: 3 / 4;
    width: 35rem;

}

.create-room {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(222, 237, 255, 0.08);
    border-radius: 14px;


    > p:nth-child(1) {
        @include title--font;
        margin: 13% 0% 10% 0%;
    }

    > form:nth-child(2) {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 90%;


        > div {
            display: flex;
            flex-direction: row;
            align-items: center;
            height: 3.6rem;
            width: 90%;
            margin: 0% 0% 8% 0%;

            border: 2px solid #595E76;
            border-radius: 25px;
            img {
                width: 2rem;
                height: 2rem;
                margin: 0% 5% 0% 4%;
            }
            input {
                background-color: transparent;
                border: none;
                @include p--font;
                font-size: 1rem;
                color: white;
                width: 70%;
                height: 100%;
            }
            > input:focus-within {
                outline: none;
                background-color: transparent !important;
            }
        }
        
        > button:nth-child(3) {
            @include button--font();
            border: none;
            background: #4289F3;
            border-radius: 10px;
            align-self: center;
            width: 90%;
            padding: .75rem 0;
            margin: 7% 0% 8% 0%;

        }
    }
}


@media (max-width: 1000px){
    .create-room {
        grid-row: 1 / 2;
        grid-column: 1 / 4;
        width: 70%;
    }

    #blur {
        filter: blur(3px);
    }

    html {
        font-size: 14px;
    }
}

@media (max-width: 650px){
    html {
        font-size: 12px;
    }
  
}



@media (max-width: 400px){
    html {
        font-size: 10px;
    }
}
